<template>
    <div class="main-wrapper">
        <layout-header></layout-header>
        <!-- <breadcrumb13></breadcrumb13> -->
        <!-- Page Content -->
			<div class="content">
				<div class="container">
				
					<div class="row">
						<div class="col-lg-12 col-md-12">
							<div class="blog-view">
								<div class="blog blog-single-post">
									 

                                     <div class="jumbotron mt-3">
        <h1 class="mb-5">{{ quiz.title }}</h1>
        <hr>
        <p v-if="errors[questionIndex]" class="alert alert-danger">
            {{ error }}
        </p>
        <div v-for="(question, index) in quiz.questions" :key="question.id">
          <div v-show="index === questionIndex">
           
            <h4 class="mt-5 mb-3">{{ question.text }}</h4> 
            
            <div v-for="answer in question.answers" class="form-check" :key="answer.id">
              <label class="form-check-label">
                <input class="form-check-input" type="radio"
                     :value="answer.value"
                     :name="index"
                     v-model="responses[index]">
                {{answer.text}}
              </label>
            </div>
            
            <div class="mt-5">
              <button 
                 class="btn btn-primary" 
                 v-if="questionIndex > 0" 
                 @click="prev">
                  prev
              </button>
              <button class="btn btn-secondary" @click="next">
                  next
              </button>  
            </div>
          </div>
        </div>
        
        <div v-show="questionIndex === quiz.questions.length">
          <h3>Your Results</h3>
          <p>
            You are: {{ quiz.questions.answers }}
          </p>
          
          <button class="btn btn-success" @click="playAgain">
            Play Again!
          </button>
        </div>
      </div>






								</div>
							</div>
						</div>
                </div>
				</div>

			</div>		
			<!-- /Page Content -->
            <layout-footer></layout-footer>
    </div>
</template>
<script>

export default {
    data() {
        return {

            quiz : {
            title: "GAD-7",
            questions: [
                {
                text: 'Feeling nervous, anxious or on edge',
                answers: [
                    {
                    text: 'Not at all',
                    value: 1
                    },
                    {
                    text: 'Several days',
                    value: 2
                    },
                    {
                    text: 'More than half the days',
                    value: 3
                    },
                    {
                    text: 'Nearly every day',
                    value: 4
                    },
                ]
                },
                {
                text: 'Not being able to stop or control worrying',
                answers: [
                    {
                    text: 'Not at all',
                    value: 1
                    },
                    {
                    text: 'Several days',
                    value: 2
                    },
                    {
                    text: 'More than half the days',
                    value: 3
                    },
                    {
                    text: 'Nearly every day',
                    value: 4
                    },
                ]
                },
                {
                text: 'Worrying too much about different things',
                answers: [
                    {
                    text: 'Not at all',
                    value: 1
                    },
                    {
                    text: 'Several days',
                    value: 2
                    },
                    {
                    text: 'More than half the days',
                    value: 3
                    },
                    {
                    text: 'Nearly every day',
                    value: 4
                    },
                ]
                },
                {
                text: 'Trouble relaxing',
                answers: [
                    {
                    text: 'Not at all',
                    value: 1
                    },
                    {
                    text: 'Several days',
                    value: 2
                    },
                    {
                    text: 'More than half the days',
                    value: 3
                    },
                    {
                    text: 'Nearly every day',
                    value: 4
                    },
                ]
                },
                {
                text: 'Being so restless that it is hard to sit still',
                answers: [
                    {
                    text: 'Not at all',
                    value: 1
                    },
                    {
                    text: 'Several days',
                    value: 2
                    },
                    {
                    text: 'More than half the days',
                    value: 3
                    },
                    {
                    text: 'Nearly every day',
                    value: 4
                    },
                ]
                },
                {
                text: 'Becoming easily annoyed or irritable',
                answers: [
                    {
                    text: 'Not at all',
                    value: 1
                    },
                    {
                    text: 'Several days',
                    value: 2
                    },
                    {
                    text: 'More than half the days',
                    value: 3
                    },
                    {
                    text: 'Nearly every day',
                    value: 4
                    },
                ]
                },
                {
                text: 'Feeling afraid as if something awful might happen',
                answers: [
                    {
                    text: 'Not at all',
                    value: 1
                    },
                    {
                    text: 'Several days',
                    value: 2
                    },
                    {
                    text: 'More than half the days',
                    value: 3
                    },
                    {
                    text: 'Nearly every day',
                    value: 4
                    },
                ]
                }
        ]
    },

           
            
            questionIndex: 0,
            responses: [],
            errors: [],
            error: ''
      
        }
    },
    methods: {
    prev: function() {
      this.questionIndex--;
    },
    
    next: function() {
      if (this.responses[this.questionIndex] === undefined) {
        this.errors[this.questionIndex] = 1;
        this.error = 'Please select your answer';
      } 
      else {
        this.errors[this.questionIndex] = 0;
        this.questionIndex++;
      } 
    },
    
    score: function() {
      //return questions.answers;
    },
    
    playAgain: function() {
      this.questionIndex = 0;
    }
  }
    
}
</script>
<style scoped>
.form-check-input:checked{
    background-color: #E1AD01;
}
.form-check {
    position: relative;
    display: block;
    padding-left: 1.25rem;
    margin-top: 10px;
}
.form-check-input {
    position: absolute;
    margin-left: -1.6rem;
    width: 1.25rem;
    height: 1.25rem;
    margin-top: 0px !important;
}
</style>





